import { getType } from "typesafe-actions";
import { ValidAuthAction, validAuthActions } from "../actions";

const isValidAuth = (state = false, action: ValidAuthAction) => {
  switch (action.type) {
    case getType(validAuthActions.invalidateAuth):
      return false;
    case getType(validAuthActions.validateAuth):
      return true;
    default:
      return state;
  }
};

export default isValidAuth;



