import auth0 from "auth0-js";

class Auth {
  public userProfile: auth0.Auth0UserProfile | null;
  public requestedScopes = "openid email read:messages";
  public tokenRenewalTimeout: NodeJS.Timer;
  public authOptions: auth0.AuthOptions = {
    audience: process.env.REACT_APP_AUTH0_AUDIENCE || "",
    clientID: process.env.REACT_APP_AUTH0_CLIENT_ID || "",
    domain: process.env.REACT_APP_AUTH0_DOMAIN || "",
    redirectUri: process.env.REACT_APP_AUTH0_REDIRECT_URI || "",
    responseType: "token id_token",
    scope: this.requestedScopes
  };
  public auth0 = new auth0.WebAuth(this.authOptions);

  constructor() {
    this.login = this.login.bind(this);
    this.logout = this.logout.bind(this);
    this.handleAuthentication = this.handleAuthentication.bind(this);
    this.isAuthenticated = this.isAuthenticated.bind(this);
    this.userHasScopes = this.userHasScopes.bind(this);
    this.getAccessToken = this.getAccessToken.bind(this);
    this.userProfile = null;
    this.getProfile = this.getProfile.bind(this);
    this.scheduleRenewal();
  }

  public login(): void {
    this.auth0.authorize();
  }

  public handleAuthentication() {
    const self = this;
    return new Promise((resolve, reject) => {
      self.auth0.parseHash((err, authResult) => {
        if (authResult && authResult.accessToken && authResult.idToken) {
          resolve(self.setSession(authResult));
        } else if (err) {
          reject(err);
        } else {
          reject("Failed to handle auth");
        }
      });
    });
  }

  public setSession(authResult: auth0.Auth0DecodedHash) {
    return new Promise((resolve, reject) => {
      if (
        authResult.expiresIn &&
        authResult.accessToken &&
        authResult.idToken
      ) {
        // Set the time that the access token will expire at
        const expiresAt = JSON.stringify(
          authResult.expiresIn * 1000 + new Date().getTime()
        );
        // If there is a value on the `scope` param from the authResult,
        // use it to set scopes in the session for the user. Otherwise
        // use the scopes as requested. If no scopes were requested,
        // set it to nothing
        const scopes = authResult.scope || this.requestedScopes || "";

        localStorage.setItem("access_token", authResult.accessToken);
        localStorage.setItem("id_token", authResult.idToken);
        localStorage.setItem("expires_at", expiresAt);
        localStorage.setItem("scopes", JSON.stringify(scopes));

        // schedule a token renewal
        this.scheduleRenewal();
        resolve(this.getProfile());
      } else {
        reject("Failed to set session");
      }
    });
  }

  public getAccessToken() {
    const accessToken = localStorage.getItem("access_token");
    if (!accessToken) {
      throw new Error("No access token found");
    }
    return accessToken;
  }

  public getProfile() {
    const accessToken = this.getAccessToken();
    return new Promise((resolve, reject) => {
      this.auth0.client.userInfo(accessToken, (err, profile) => {
        if (profile) {
          this.userProfile = profile;
          resolve({ profile });
        } else if (err) {
          reject(err);
        } else {
          reject("Failed to get profile");
        }
      });
    });
  }

  public logout() {
    // Clear Access Token and ID Token from local storage
    localStorage.removeItem("access_token");
    localStorage.removeItem("id_token");
    localStorage.removeItem("expires_at");
    localStorage.removeItem("scopes");
    this.userProfile = null;
    clearTimeout(this.tokenRenewalTimeout);
  }

  public isAuthenticated() {
    // Check whether the current time is past the
    // Access Token's expiry time
    const eA = localStorage.getItem("expires_at");
    if (eA) {
      const expiresAt = JSON.parse(eA);
      return new Date().getTime() < expiresAt;
    } else {
      return false;
    }
  }

  public userHasScopes(scopes: string[]) {
    const s = localStorage.getItem("scopes");
    if (s) {
      const grantedScopes = (JSON.parse(s) || "").split(" ");
      return scopes.every(scope => grantedScopes.includes(scope));
    } else {
      return false;
    }
  }

  public renewToken() {
    this.auth0.checkSession({}, (err, result) => {
      if (err) {
        /* console.log(
          `Could not get a new token (${err.error}: ${err.errorDescription}).`
        ); */
      } else {
        this.setSession(result);
        // console.log(`Successfully renewed auth!`);
      }
    });
  }

  public scheduleRenewal() {
    const eA = localStorage.getItem("expires_at");
    if (eA) {
      const expiresAt = JSON.parse(eA);
      const delay = expiresAt - Date.now();
      if (delay > 0) {
        this.tokenRenewalTimeout = setTimeout(() => {
          this.renewToken();
        }, delay);
      }
    } else {
      // console.log("Cannot renew schedule: no idea when token expires");
    }
  }
}

const auth = new Auth();

export default auth;



