import AppBar from "@material-ui/core/AppBar";
import Button from "@material-ui/core/Button";
import Collapse from "@material-ui/core/Collapse";
import CssBaseline from "@material-ui/core/CssBaseline";
import Divider from "@material-ui/core/Divider";
import Drawer from "@material-ui/core/Drawer";
import Hidden from "@material-ui/core/Hidden";
import IconButton from "@material-ui/core/IconButton";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import { createStyles, Theme, WithStyles } from "@material-ui/core/styles";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import DirectionsRunIcon from "@material-ui/icons/DirectionsRun";
import GetAppIcon from "@material-ui/icons/GetApp";
import MenuIcon from "@material-ui/icons/Menu";
import { mainListItems, otherListItems } from "./constants";
import logo from "./logo.svg";

import React from "react";

const drawerWidth = 240;

export const styles = (theme: Theme) =>
  createStyles({
    "@keyframes App-logo-spin": {
      from: { transform: "rotate(0deg)" },
      to: { transform: "rotate(360deg)" }
    },
    appBar: {
      marginLeft: drawerWidth,
      position: "absolute",
      [theme.breakpoints.up("md")]: {
        width: `calc(100% - ${drawerWidth}px)`
      }
    },
    appLogo: {
      animation: "App-logo-spin infinite 20s linear",
      height: "32px"
    },
    button: {
      align: "right",
      margin: theme.spacing.unit
    },
    content: {
      backgroundColor: theme.palette.background.default,
      flexGrow: 1,
      padding: theme.spacing.unit * 3
    },
    drawerPaper: {
      width: drawerWidth,
      [theme.breakpoints.up("md")]: {
        position: "relative"
      }
    },
    navIconHide: {
      [theme.breakpoints.up("md")]: {
        display: "none"
      }
    },
    rightIcon: {
      marginLeft: theme.spacing.unit
    },
    root: {
      display: "flex",
      flexGrow: 1,
      height: "100%",
      overflow: "hidden",
      position: "relative",
      width: "100%",
      zIndex: 1
    },
    toolbar: theme.mixins.toolbar,
    toolbarSpace: {
      flexGrow: 1
    }
  });

export interface IAppProps extends WithStyles<typeof styles> {
  children: React.ReactChildren;
  isValidAuth: boolean;
  logout: () => void;
  checkAuth: () => void;
}

export interface IAppState {
  a2hsPrompt: boolean;
  deferredPrompt: any;
  mobileOpen: boolean;
}

class AppComponent extends React.Component<IAppProps, IAppState> {
  constructor(props: IAppProps) {
    super(props);
    this.state = {
      a2hsPrompt: false,
      deferredPrompt: null,
      mobileOpen: false
    };
  }

  public addToHomeScreen = () => {
    // Hide our user interface that shows our A2HS button
    this.setState({ a2hsPrompt: false });

    const deferredPrompt = this.state.deferredPrompt;
    if (deferredPrompt) {
      // Show the prompt
      deferredPrompt.prompt();
      // Wait for the user to respond to the prompt
      deferredPrompt.userChoice.then((choiceResult: any) => {
        if (choiceResult.outcome === "accepted") {
          console.log("User accepted the A2HS prompt"); // tslint:disable-line
        } else {
          console.log("User dismissed the A2HS prompt"); // tslint:disable-line
        }
        this.setState({ deferredPrompt: null });
      });
    }
  };

  public handleBeforeInstallPrompt = (e: Event) => {
    // Prevent Chrome 67 and earlier from automatically showing the prompt
    e.preventDefault();
    // Stash the event so it can be triggered later, and
    // update UI to notify the user they can add to home screen.
    this.setState({ deferredPrompt: e, a2hsPrompt: true });
  };

  public componentDidMount() {
    window.addEventListener(
      "beforeinstallprompt",
      this.handleBeforeInstallPrompt
    );
    this.props.checkAuth();
  }

  public componentWillUnmount() {
    window.removeEventListener(
      "beforeinstallprompt",
      this.handleBeforeInstallPrompt
    );
  }

  public handleDrawerToggle = () => {
    this.setState({ mobileOpen: !this.state.mobileOpen });
  };

  public render() {
    const { classes, theme, children, isValidAuth, logout } = this.props;
    const { a2hsPrompt, mobileOpen } = this.state;

    const drawer = (
      <div>
        <div className={classes.toolbar} />
        <Divider />
        <List>{mainListItems}</List>
        <Divider />
        <List>{otherListItems}</List>
        <Divider />
        <Collapse in={a2hsPrompt}>
          <List>
            <ListItem button={true} onClick={this.addToHomeScreen}>
              <ListItemIcon>
                <GetAppIcon />
              </ListItemIcon>
              <ListItemText primary="Add to home" />
            </ListItem>
          </List>
        </Collapse>
      </div>
    );

    return (
      <React.Fragment>
        <CssBaseline />
        <div className={classes.root}>
          <AppBar className={classes.appBar}>
            <Toolbar>
              <IconButton
                color="inherit"
                aria-label="open drawer"
                onClick={this.handleDrawerToggle}
                className={classes.navIconHide}
              >
                <MenuIcon />
              </IconButton>
              <Typography variant="title" color="inherit" noWrap={true}>
                HORTON GUI
              </Typography>
              <img src={logo} className={classes.appLogo} alt="logo" />
              <div className={classes.toolbarSpace} />
              {isValidAuth && (
                <Button
                  className={classes.button}
                  variant="contained"
                  color="primary"
                  onClick={logout}
                >
                  Logout<DirectionsRunIcon className={classes.rightIcon} />
                </Button>
              )}
            </Toolbar>
          </AppBar>
          <Hidden mdUp={true}>
            <Drawer
              variant="temporary"
              anchor={theme && theme.direction === "rtl" ? "right" : "left"}
              open={mobileOpen}
              onClose={this.handleDrawerToggle}
              classes={{
                paper: classes.drawerPaper
              }}
              ModalProps={{
                keepMounted: true // Better open performance on mobile.
              }}
            >
              {drawer}
            </Drawer>
          </Hidden>
          <Hidden smDown={true} implementation="css">
            <Drawer
              variant="permanent"
              open={true}
              classes={{
                paper: classes.drawerPaper
              }}
            >
              {drawer}
            </Drawer>
          </Hidden>
          <main className={classes.content}>
            <div className={classes.toolbar} />
            {children}
          </main>
        </div>
      </React.Fragment>
    );
  }
}

export default AppComponent;



