import { ConnectedRouter } from "connected-react-router";
import React from "react";
import Loadable from "react-loadable";
import { Route, Switch } from "react-router"; // react-router v4
import App from "../App";
import Loading from "../components/Loading";
import history from "../history";

const loading = ({
  error,
  retry,
  timedOut,
  pastDelay,
  isLoading
}: Loadable.LoadingComponentProps) => (
  <Loading
    error={error}
    retry={retry}
    timedOut={timedOut}
    pastDelay={pastDelay}
    isLoading={isLoading}
  />
);

const LoadableProfile = Loadable({
  loader: () => import("../components/Profile"),
  loading,
  timeout: 10000 // 10 seconds
});

const LoadableCallback = Loadable({
  loader: () => import("../containers/Callback"),
  loading,
  timeout: 10000 // 10 seconds
});

const LoadableDftForm = Loadable({
  loader: () => import("../containers/DftForm"),
  loading,
  timeout: 10000 // 10 seconds
});

const LoadableHome = Loadable({
  loader: () => import("../containers/Home"),
  loading,
  timeout: 10000 // 10 seconds
});

export const RootRouter = () => (
  <ConnectedRouter history={history}>
    <App>
      <Switch>
        <Route path="/profile" component={LoadableProfile} />
        <Route path="/callback" component={LoadableCallback} />
        <Route path="/submit-dft" component={LoadableDftForm} />
        <Route path="/home" component={LoadableHome} />
        <Route component={LoadableHome} />
      </Switch>
    </App>
  </ConnectedRouter>
);

export default RootRouter;



