import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import AddCircleIcon from "@material-ui/icons/AddCircle";
import EmailIcon from "@material-ui/icons/Email";
import HomeIcon from "@material-ui/icons/Home";
import InfoIcon from "@material-ui/icons/Info";
import WebIcon from "@material-ui/icons/Web";
import ZoomInIcon from "@material-ui/icons/ZoomIn";
import React from "react";
import { Link } from "react-router-dom";

const HomeLink: React.SFC = props => <Link to="/home" {...props} />;
const SubmitDftLink: React.SFC = props => <Link to="/submit-dft" {...props} />;
const ProfileLink: React.SFC = props => <Link to="/profile" {...props} />;

export const mainListItems = (
  <div>
    <ListItem button={true} component={HomeLink}>
      <ListItemIcon>
        <HomeIcon />
      </ListItemIcon>
      <ListItemText primary="Home" />
    </ListItem>
    <ListItem button={true} component={SubmitDftLink}>
      <ListItemIcon>
        <AddCircleIcon />
      </ListItemIcon>
      <ListItemText primary="Submit DFT" />
    </ListItem>
    <ListItem button={true} component={ProfileLink}>
      <ListItemIcon>
        <WebIcon />
      </ListItemIcon>
      <ListItemText primary="Profile" />
    </ListItem>
  </div>
);

export const otherListItems = (
  <div>
    <ListItem button={true}>
      <ListItemIcon>
        <InfoIcon />
      </ListItemIcon>
      <ListItemText primary="About HORTON" />
    </ListItem>
    <ListItem button={true}>
      <ListItemIcon>
        <ZoomInIcon />
      </ListItemIcon>
      <ListItemText primary="Learn more" />
    </ListItem>
    <ListItem button={true}>
      <ListItemIcon>
        <EmailIcon />
      </ListItemIcon>
      <ListItemText primary="Email us" />
    </ListItem>
  </div>
);



