import { replace } from "connected-react-router";
import { all, call, put, select, takeLatest } from "redux-saga/effects";
import { getType } from "typesafe-actions";
import { authActions, profileActions, validAuthActions } from "../actions";
import auth from "../auth";
import { IStoreState } from "../types";

function* login() {
  yield call(auth.login);
}

const getLocationHash = (state: IStoreState) => state.router.location.hash;

const authenticateFromHash = (locationHash: string) => {
  if (/access_token|id_token|error/.test(locationHash)) {
    return auth.handleAuthentication();
  } else {
    return Promise.reject("Failed to authenticate from hash");
  }
};

function* callback() {
  const locationHash = yield select(getLocationHash);
  yield call(authenticateFromHash, locationHash);
  const isAuthenticated = yield call(auth.isAuthenticated);
  if (isAuthenticated) {
    yield put(validAuthActions.validateAuth());
    // const profile = yield call(auth.getProfile);
    // yield put(profileActions.setProfile(profile));
  } else {
    yield put(validAuthActions.invalidateAuth());
  }
  // console.log(auth.userProfile); // tslint:disable-line

  yield put(replace("/"));
  // history.replace('/home');  // replaces '/callback' with '/home'
  // console.log(authStatus);
}

function* logout() {
  yield call(auth.logout);
  yield put(profileActions.setProfile(null));
  yield put(validAuthActions.invalidateAuth());
  yield put(replace("/"));
}

const getIsValidAuth = (state: IStoreState) => state.isValidAuth;

function* checkAuth() {
  const isAuthenticated = yield call(auth.isAuthenticated);
  const isValidAuth = yield select(getIsValidAuth);
  if (isAuthenticated !== isValidAuth) {
    if (isAuthenticated) {
      yield put(validAuthActions.validateAuth());
    } else {
      yield put(validAuthActions.invalidateAuth());
    }
  }
}

function* authWatcher() {
  yield all([
    takeLatest(getType(authActions.login), login),
    takeLatest(getType(authActions.callback), callback),
    takeLatest(getType(authActions.logout), logout),
    takeLatest(getType(authActions.checkAuth), checkAuth)
  ]);
}

export default authWatcher;



