import { ActionType, createStandardAction } from "typesafe-actions";

const validateAuth = createStandardAction("auth/VALIDATE")();
const invalidateAuth = createStandardAction("auth/INVALIDATE")();

export const validAuthActions = { validateAuth, invalidateAuth };
export type ValidAuthAction = ActionType<typeof validAuthActions>;

const login = createStandardAction("auth/LOGIN")();
const callback = createStandardAction("auth/CALLBACK")();
const logout = createStandardAction("auth/LOGOUT")();
const checkAuth = createStandardAction("auth/CHECK")();

export const authActions = { login, callback, logout, checkAuth };
export type AuthAction = ActionType<typeof authActions>;



